import React, { Component } from 'react';
import PropTypes from 'prop-types'
import { connect } from 'react-redux';
import { removeCollection } from '../actions.js';
import { Link } from 'react-router-dom'
class Collection extends Component {
    static propTypes = {
        onRemove: PropTypes.func.isRequired,
        text: PropTypes.string.isRequired
    }
    handleOnRemove() {
        const userInfo = JSON.parse(localStorage.getItem('userinfo'))
        this.props.onRemove()
        fetch('/article/removecollection', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                username: userInfo.username, collectionId: this.props.id,

            })
        }).then((ret) => {
            if (ret.status !== 200) {
                throw new Error('Fail to get response with status' + ret.status)
            }
            ret.json().then((res) => {
                if (res.status === 200) {
                    console.log('')
                }
            }).catch((error) => {
                console.log(error)
            })
        }).catch((error) => {
            console.log(error)
        })

    }
    render() {
        let { text, id } = this.props
        return (
            <li >
                <Link to={`/writer/${id}`} className="writer-collections-item">
                    <span className="text">{text}</span>
                    <div className="btn-group seting">
                        <span className="collection-setting glyphicon glyphicon-cog dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        </span>
                        <ul className="setting-wrapper dropdown-menu">
                            <li
                                data-toggle="modal" data-target="#myModal"
                                className="cancle-collection"><span className=" glyphicon glyphicon-trash" ></span> </li>
                        </ul>
                    </div>
                    <div className="modal fade bs-example-modal-sm" id="myModal" tabIndex="1" role="dialog" aria-labelledby="myModalLabel">
                        <div className="modal-dialog modal-sm" role="document">
                            <div className="modal-content">
                                <div className="modal-body">
                                    
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-default" data-dismiss="modal"></button>
                                    <button type="button" onClick={this.handleOnRemove.bind(this)} className="btn btn-danger" data-dismiss="modal"></button>
                                </div>
                            </div>
                        </div>
                    </div>
                </Link>
            </li>
        )
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    let id = ownProps.id
    return {
        onRemove: () => {
            dispatch(removeCollection(id));
        }
    };
};

export default connect(null, mapDispatchToProps)(Collection);




